/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  font-weight: 300;
  background-color: white;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
  transition-property: transform;
  transition-duration: 0.3s;
}
img:hover {
  transform: scale(1.1);
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

select {
  width: 100%;
  height: 4rem;
  border-radius: 1rem;
  text-align: center;
  font-size: 1.6rem;
}

fieldset:not(:first-of-type) {
  display: none;
}

.tabla {
  overflow-x: scroll;
  overflow-y: hidden;
  scroll-snap-type: x mandatory;
  scroll-snap-type: y mandatory;
}

table {
  width: 100%;
}

thead {
  background-color: #344888;
  color: #FFFFFF;
  text-transform: uppercase;
}

th {
  border: #000 0.1rem solid;
  padding: 1rem 0;
}

tbody {
  margin-bottom: 1rem;
}

tbody tr:nth-child(even) {
  background-color: #e0ecf4;
  padding: 1 0;
}

tbody tr {
  transition-property: background-color;
  transition-duration: 0.3s;
}
tbody tr:hover {
  background-color: #d5e5ef;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1, h2 {
  font-weight: 400;
}

h1 {
  font-size: 4.4rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 2.4rem;
  font-weight: 300;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  font-size: 1.6rem;
  line-height: 1.8;
}

.bg-negro {
  background-color: #000;
}

.alerta__error {
  border-color: #f5c2c7;
  border-width: 0.1rem;
  border-style: solid;
  background-color: #f8d7da;
  text-align: center;
  padding: 2rem;
  margin-bottom: 1rem;
  border-radius: 1rem;
  color: #842029;
}
.alerta__success {
  border-color: #badbcc;
  border-width: 0.1rem;
  border-style: solid;
  background-color: #d1e7dd;
  text-align: center;
  padding: 2rem;
  margin-bottom: 1rem;
  border-radius: 1rem;
  color: #0f5132;
}

.modal {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 180%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(79, 79, 79, 0.7);
  z-index: 1;
  visibility: hidden;
}
@media (min-width: 768px) {
  .modal {
    height: 130%;
    align-items: flex-start;
  }
}
.modal__contenido {
  width: 70%;
  height: auto;
  padding: 10px;
  border-radius: 10px;
  background-color: aliceblue;
  text-align: center;
}
@media (min-width: 768px) {
  .modal__contenido {
    margin-top: 15rem;
  }
}
.modal__cerrar {
  text-align: right;
  cursor: pointer;
  font-size: 3rem;
  padding-right: 1rem;
}
.modal__close {
  display: flex;
  justify-content: flex-end;
}
.modal__boton {
  background-color: #71a7ca;
  font-size: uppercase;
  cursor: pointer;
  padding: 1rem;
  color: #FFFFFF;
  border-radius: 1rem;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
}
.modal__boton:hover {
  background-color: #4c90bc;
}

.header {
  background-size: cover;
  background-position: center center;
  background-attachment: fixed;
}
.header__texto {
  padding: 7rem 0 15rem 0;
  text-align: center;
  color: #71a7ca;
}

.navegacion {
  padding: 1.5rem 5rem;
  background-color: #344888;
  align-items: center;
}
.navegacion__contenido {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}
@media (min-width: 768px) {
  .navegacion__contenido {
    flex-direction: row;
  }
}
.navegacion__enlace {
  color: #FFFFFF;
  text-align: center;
  font-size: 1.5rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .navegacion__enlace {
    margin-bottom: 0rem;
  }
}
.navegacion__enlace:hover {
  color: #71a7ca;
}
.navegacion__enlace--activa {
  font-weight: 900;
  color: #71a7ca;
  border-bottom: 1px solid #71a7ca;
}
.navegacion__enlace--acceso {
  font-weight: 400;
  margin-top: 0.3rem;
  font-size: 1.3rem;
}
.navegacion__boxes {
  display: flex;
  flex-direction: row;
}
.navegacion__box {
  color: #FFFFFF;
  border-width: 1px;
  border-style: solid;
  border-color: #FFFFFF;
  border-radius: 1rem;
  padding: 1rem;
  cursor: pointer;
  margin: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.navegacion__box--icono {
  width: 3rem;
  height: 3rem;
  padding-bottom: 0.3rem;
}
.navegacion__box:hover {
  color: #71a7ca;
  border-color: #71a7ca;
}

.main {
  height: 35rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.main__imagen {
  height: 50%;
  width: 50%;
}

.creacion {
  display: flex;
  flex-direction: column;
}
.creacion__titulo {
  text-align: center;
  margin: 2rem 0;
}
@media (min-width: 768px) {
  .creacion__titulo {
    text-align: left;
  }
}
.creacion__opciones {
  font-weight: 900;
  display: flex;
  flex-direction: row;
  gap: 4rem;
}

@media (min-width: 768px) {
  .formulario__pasos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(5, 1fr);
  }
}
.formulario__registro {
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .formulario__registro:nth-child(1) {
    grid-column: 1/6;
  }
  .formulario__registro:nth-child(2) {
    grid-column: 1/3;
  }
  .formulario__registro:nth-child(3) {
    grid-column: 1/3;
  }
  .formulario__registro:nth-child(4) {
    grid-column: 1/2;
  }
  .formulario__registro:nth-child(6) {
    grid-column: 3/5;
    grid-row: 2/3;
  }
  .formulario__registro:nth-child(7) {
    grid-column: 5/6;
    grid-row: 2/3;
    display: flex;
    align-items: flex-end;
  }
  .formulario__registro:nth-child(8) {
    grid-column: 2/4;
    grid-row: 5/6;
  }
}
.formulario__cronograma:nth-child(1) {
  grid-column: 1/6;
}
.formulario__cronograma:nth-child(6) {
  grid-column: 2/5;
  grid-row: 3/4;
}
.formulario__cronograma:nth-child(7) {
  grid-column: 3/4;
}
.formulario__label {
  display: block;
  font-size: 1.8rem;
  margin-bottom: 1rem;
}
.formulario__campo {
  width: 100%;
  padding: 1rem;
  border-radius: 1rem;
  font-size: 1.6rem;
}
.formulario__submit {
  background-color: #71a7ca;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  border-radius: 2rem;
}
.formulario__submit:hover {
  background-color: #4c90bc;
}
.formulario__submit--siguiente {
  grid-column: 5/6;
  grid-row: 5/6;
}
.formulario__submit--previo {
  grid-column: 2/3;
  background-color: #96bed8;
  margin: 1rem 0;
}
@media (min-width: 768px) {
  .formulario__submit--previo {
    margin: 0;
  }
}
.formulario__submit--actividad {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}
.formulario__submit--icono {
  width: 3rem;
  height: 3rem;
}
.formulario__buscar-actividad {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(3, 1fr);
  margin-bottom: 1rem;
}
.formulario__registro-actividad:nth-child(1) {
  grid-column: 1/3;
}
.formulario__registro-actividad:nth-child(2) {
  grid-column: 3/4;
  display: flex;
  align-items: center;
}
.formulario__registro-actividad:nth-child(3) {
  grid-column: 1/4;
}

.footer {
  background-color: #344888;
  padding: 10rem 0;
}
.footer__grid {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .footer__grid {
    flex-direction: row-reverse;
    justify-content: space-between;
    align-items: center;
  }
}